(function($) {
  $(document).ready(function() {
    console.log('ready');
    // Fixed phone btn
    $('#phone-btn').on('click', function() {
      if ($('#floating-phone-btns').hasClass('active')) {
        $('#floating-phone-btns').removeClass('active');
      } else {
        $('#floating-phone-btns').addClass('active');
      }
    });

    // Projects Carousel
    $('.projects-carousel').slick({
      slidesToShow: 4,
      slidesToScroll: 1,
      arrows: true,
    });

    // Fixed header
    $(document).on('scroll', function(e) {
      let scrollTop = $(this).scrollTop();
      let width = $(document).width();
      if (scrollTop >= 330 && width >= 1000) {
        $('#fixed-header').addClass('active animated fadeInDown slow');
      } else {
        $('#fixed-header').removeClass('active animated fadeInDown slow');
      }
    });
    // Trigger scroll on page load to trigger if saved position exist
    $(document).trigger('scroll');

    // Tabs
    $('.tab-control-button').on('click', function() {
      $('.tab-control-button').removeClass('active');
      $(this).addClass('active');

      const index = $(this)
        .parent()
        .index();

      let tabs = $('.tabs-body').find('.tab-content');
      // eq() reduces jquery objects array to get jquery object
      // get() or [index] returns DOM object
      let tab = tabs.eq(index);

      tabs.removeClass('active');
      tab.addClass('active');
    });

    // Mobile header
    $('#hamburger').on('click', function() {
      let isOpen = $(this).hasClass('active');
      if (!isOpen) {
        $(this).addClass('active');
        $('#fixed-nav-menu').addClass('active');
      } else {
        $(this).removeClass('active');
        $('#fixed-nav-menu').removeClass('active');
      }
    });
  });
})(jQuery);
